
"use strict";
import render from './render';
import {fetchLogin, fetchLogout, fetchSession, fetchStoredWord, fetchSetWord} from './services';
import {state, errorMessages} from './state';

const rootEl = document.querySelector('#root');
const errorEl = document.querySelector('.error');

function handleWordPage(){
    fetchStoredWord()
    .then(response => {
        state.username = response.username;
        state.storedWord = response.storedWord;
        render(state, rootEl);
        errorEl.innerHTML = '';
    })
    .catch(err => { 
        const errorMessage = errorMessages[err.error];
        errorEl.innerHTML = `<p>${errorMessage}</p>`;
        state.username = '';
        state.storedWord = '';
        render(state, rootEl);
    })
}

function handleSession(){
    fetchSession()
    .then(response => {
        handleWordPage();
        errorEl.innerHTML = '';
    })
    .catch(err => { 
        render(state, rootEl);
    })
}

function handleLogin(){
    const username = document.querySelector('.username').value;
    fetchLogin(username)
    .then(() => {
        handleWordPage();
    })
    .catch(
        err => {
            const errorMessage = errorMessages[err.error];
            errorEl.innerHTML = `<p>${errorMessage}</p>`;
        }
    )
}

function changeWord(){
    const word = document.querySelector('.newWord').value;
    fetchSetWord(word)
    .then(response => {
        state.storedWord = response.storedWord;
        render(state, rootEl);
        errorEl.innerHTML = '';
    })
    .catch(
        err => {
            // if is invalid seesion id we need to render to the login page
            if (err.error === 'auth-missing'){
                state.username = '';
                state.storedWord = '';
                render(state, rootEl);
            }
            const errorMessage = errorMessages[err.error];
            errorEl.innerHTML = `<p>${errorMessage}</p>`;
        }
    )
}

function handleLogout(){
    fetchLogout()
    .then(() => {
        state.username = '';
        state.storedWord = '';
        render(state, rootEl);
        errorEl.innerHTML = '';
    })
    .catch(
        err => {
            const errorMessage = errorMessages[err.error];
            errorEl.innerHTML = `<p>${errorMessage}</p>`;
        }
    )
}

handleSession();
rootEl.addEventListener('click', (e) => {
    if (e.target.classList.contains('login-button')){
        handleLogin();
    }
    else if (e.target.classList.contains('change-word')){
        changeWord();
    }
    else if (e.target.classList.contains('logout-button')){
        handleLogout();
    }
})

